import React, {Component} from 'react'
import Cookies from 'js-cookie';
import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';


class RecipeForm extends Component{
    constructor (props){
          super(props);
          this.state = {
            title: "",
            body: "",
            author: "",
            preptime: "",
            cooktime: "",
            cooktemp: "",
            amount: "",
            units: "",
            ingredient: "",
            ingredientList: "",
            value: "",
            qty: "",
            weightScale: "",
            profile_picture: "",
          }
          this.handleInput = this.handleInput.bind(this);
          this.handleSubmit = this.handleSubmit.bind(this);
          this.addIngredient = this.addIngredient.bind(this);
          this.addStep = this.addStep.bind(this);
          this.handleChange = this.handleChange.bind(this);
          this.handleImage = this.handleImage.bind(this);
          this.submitPhoto = this.submitPhoto.bind(this);
            }

handleInput(event){
    this.setState({[event.target.name]: event.target.value});
  }

  handleChange(event) {
      this.setState({value: event.target.value});
      console.log(this.state)
    }

addIngredient(e){
  e.preventDefault();
  let str = this.state.ingredient
  let ing = this.state.ingredientList
  ing = `${ing} ${this.state.qty} ${this.state.weightScale}  ${str}  \n`
  this.setState({ingredientList: ing})
  this.setState({ingredient: ""})
  this.setState({qty: ""})
}

addStep(e){
  e.preventDefault();
  let step = this.state.directions
  let str = this.state.body
  str = `${str} ${step} \n`
  this.setState({body: str})
}


async handleSubmit(e){
  e.preventDefault();
  // let formData = new FormData();
  // let obj = {...this.state}
  // obj.ingredientList = JSON.stringify(obj.ingredientList)
  // for (const prop in obj){
  //   formData.append(prop, obj[prop]);
  // };

  let formData = new FormData();
  let obj = {...this.state}
  obj.ingredientList = JSON.stringify(obj.ingredientList)
  for (const prop in obj){
    formData.append(prop, obj[prop]);
  };

  const options = {
    method: 'POST',
    headers: {
      'X-CSRFToken': Cookies.get('csrftoken'),
    },
    body: formData,
  };

  const handleError = (err) => console.warn(err);
  const response = await fetch('', options);
  await response.json().catch(handleError);
}


async submitPhoto(e){
  e.preventDefault();

  let formData = new FormData();
  formData.append('profile_picture', this.state.profile_picture);
  formData.append('user', 1);


const options = {
  method: 'POST',
  headers: {
    'X-CSRFToken': Cookies.get('csrftoken'),
  },
  body: formData,
}

await fetch('', options);
}

handleImage(e){

let file = e.target.files[0]
this.setState({profile_picture: file,})

let reader = new FileReader()
reader.onloadend = () => {
  this.setState({
    preview: reader.result
  });
}

reader.readAsDataURL(file);
}










render() {


  const photoSubmit = <form id="photoForm" onSubmit={this.submitPhoto}>Attach a photo<br/><input id="photoFileInput" type="file" name="profile_picture" onChange={this.handleImage}/>
                      {this.state.profile_picture && <img width="500" src={this.state.preview} alt="preview" />}
                      </form>






  const ingredientInput = <form onSubmit={this.handleSubmit} className="ingredientForm">
          <input className="input-group form-control" type="text" placeholder="Add an ingredient" name="ingredient" value={this.state.ingredient} onChange={this.handleInput}></input>
          <input placeholder="Qty" name="qty" value={this.state.qty} className="input-group form-control" onChange={this.handleInput}></input>
          <section className="input-group form-control">
              <label >
                Weight Scale
                <select className="btn-sm btn-secondary dropdown-toggle" value={this.state.weightScale} onChange={this.handleChange}>
                  <option value="none">Select</option>
                  <option value="ounces">Ounces</option>
                  <option value="pounds">Pounds</option>
                  <option value="grams">Grams</option>
                </select>
              </label>
              </section>

          <textarea className="input-group form-control" type="text" rows="4" placeholder="Add a step to the recipe" name="directions" value={this.state.directions} onChange={this.handleInput}></textarea>
          <div className="buttonRow"><button className="btn btn-light" onClick={this.addIngredient}>Add Ingredient</button><button className="btn btn-light" onClick={this.addStep}>Add another step</button>
          <button className="btn btn-success" type="submit">Save this Recipe!</button></div>
          </form>
  const ingredientWindow = <p><textarea readOnly className="form-control" type="text" placeholder="Ingredient List" name="ingredientList" rows="5" value={this.state.ingredientList}/></p>



  const submitForm = (<form onSubmit={this.handleSubmit}>

        <input className="input-group form-control" type="text" placeholder="Recipe Name" name="title" value={this.state.title} onChange={this.handleInput}/>
        <input className="input-group form-control" type="text" placeholder="Prep Time" name="preptime" value={this.state.preptime} onChange={this.handleInput}/>
        <input className="input-group form-control" type="text" placeholder="Cook Time" name="cooktime" value={this.state.cooktime} onChange={this.handleInput}/>
        <input className="input-group form-control" type="text" placeholder="Cook Temp" name="cooktemp" value={this.state.cooktemp} onChange={this.handleInput}/>
            <section className="input-group form-control">
                <label >
                  Temp Scale
                  <select className="btn-sm btn-secondary dropdown-toggle" value={this.state.value} onChange={this.handleChange}>
                    <option value="fahrenheit">Fahrenheit</option>
                    <option value="celsius">Celsius</option>
                    <option value="kelvin">Kelvin</option>
                  </select>
                </label>
                </section>

        <input className="input-group form-control" type="text" placeholder="Total Yield" name="amount" value={this.state.amount} onChange={this.handleInput}/>
        <input className="input-group form-control" type="text" placeholder="Units" name="units" value={this.state.units} onChange={this.handleInput}/>
        <textarea readOnly className="input-group form-control" rows="5" type="text" placeholder="Recipe Body" name="body" value={this.state.body} onChange={this.handleInput}></textarea>
        <textarea className="input-group form-control" rows="3" type="text" placeholder="Notes" name="notes" value={this.state.notes} onChange={this.handleInput}></textarea>

        </form>)


  return (
    <div className="addRecipeForm">
    {photoSubmit}
    {submitForm}
    {ingredientWindow}
    {ingredientInput}
    </div>
  );
}
}

export default RecipeForm;
